import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { Observable } from 'rxjs';
import { LookUpDto } from 'src/app/models/LookUps/LookUp';
import { IRequest } from 'src/app/models/Request';
import { LookupTypeEnum } from 'src/app/enums/LookupTypeEnum';


@Injectable({
  providedIn: 'root'
})
export class LookUps {

  constructor(
    private http: HttpClient
  ) { }

  getAllNationalities(): Observable<IRequest<LookUpDto>> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetAllNationalities";
    return this.http.get<IRequest<LookUpDto>>(url_);
  }

  getAllCountries(): Observable<IRequest<LookUpDto>> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetAllCountries";
   
    return this.http.get<IRequest<LookUpDto>>(url_ );
  }

  getAllRegions(filter: string | null | undefined, ParentId: number | null | undefined ): Observable<IRequest<LookUpDto>> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetAllRegions?";
   
    if (filter !== undefined)
    url_ += "Filter=" + encodeURIComponent("" + filter) + "&";
    if (ParentId !== undefined)
      url_ += "ParentId=" + encodeURIComponent("" + ParentId) + "&";
    url_ = url_.replace(/[?&]$/, "");
    return this.http.get<IRequest<LookUpDto>>(url_);
  }

  getAllCities(filter: string | null | undefined, ParentId: number | null | undefined ): Observable<IRequest<LookUpDto>> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetAllCities?";
   
    if (filter !== undefined)
      url_ += "Filter=" + encodeURIComponent("" + filter) + "&";
    if (ParentId !== undefined)
      url_ += "ParentId=" + encodeURIComponent("" + ParentId) + "&";
    url_ = url_.replace(/[?&]$/, "");
    return this.http.get<IRequest<LookUpDto>>(url_ );
  }


  getAllDistricts(filter: string | null | undefined, ParentId: number | null | undefined ): Observable<IRequest<LookUpDto>> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetAllDistricts?";
   
    if (filter !== undefined)
      url_ += "Filter=" + encodeURIComponent("" + filter) + "&";
    if (ParentId !== undefined)
      url_ += "ParentId=" + encodeURIComponent("" + ParentId) + "&";
    url_ = url_.replace(/[?&]$/, "");
    return this.http.get<IRequest<LookUpDto>>(url_);
  }

   /**
     * @return Success
     */
    getLookupsForDropDown(lookupTypeEnum: LookupTypeEnum): Observable<any> {
      let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetLookupsForDropDown?";
     

      if (lookupTypeEnum === undefined || lookupTypeEnum === null)
          throw new Error("The parameter 'lookupTypeEnum' must be defined and cannot be null.");
      else
          url_ += "lookupTypeEnum=" + encodeURIComponent("" + 13) + "&"; 
      url_ = url_.replace(/[?&]$/, "");

      return this.http.get<any>(url_ );
  }


  getPatientRelation(lookupTypeEnum : number ) : Observable<any>{
    let url_ = BaseUrl.baseUrl + "/api/services/app/CommonLookup/GetLookupsForDropDown?";
   

    if (lookupTypeEnum !== undefined)
      url_ += "lookupTypeEnum=" + encodeURIComponent("" + lookupTypeEnum) + "&";
    url_ = url_.replace(/[?&]$/, "");
    return this.http.get<IRequest<LookUpDto>>(url_ );

  }

}
